  <div class="home-page">
    <section class="col-full bg1 masthead">
      <div class="container" style="position:relative;z-index:1">
        <div class="row part middle-xs">
          <div class="col-xs-12 col-sm-3 col-md-2">
          </div>
          <div class="col-xs-12 col-sm-9 col-md-8">
            <h1 class="title-page">Ads Sequentially Instream</h1>
            <agile :options="agileOpts" class="masthead-bg">
              <div>
                <h2>Overview</h2>
                <h3>Ads show for all player video. It’s lightweight, flexible and fast.</h3>
              </div>
              <div>
                <h2>Lightweight</h2>
                <h3>The modular structure provides excellent compression. Oh yes, it weights only ~7kb with every functionality included.</h3>
              </div>
              <div>
                <h2>Dependency</h2>
                <h3>It's uses the library of IMA SDK (supports: vast, vpaid, vmap ... )</h3>
              </div>
            </agile>
          </div>
          <div class="col-xs-12 col-sm-12 col-md-2">
