      </div>
      <div class="waves"></div>

      <router-link class="nav-menu" :to="'/'">Outstream</router-link>
    </section>

    <section class="container">
      <div class="row part center-xs">

          <div class="col-xs-12 col-sm-12 col-md-8">
            <h2 class="panel-title">Inspector</h2>
            <Form :label-width="110" mode="block">
                <!-- <FormItem label="Ad Active" single>
                  <div class="h-input-group">
                    <FormItem label="Key Active" :show-label="false" prop="adsIMAactive">
                      <input type="text" placeholder="Enter any key active" v-model="adsIMAactive" />
                    </FormItem>
                  </div>
                </FormItem> -->
                <FormItem label="Player Wrapper" single>
                  <div class="h-input-group">
                    <FormItem label="Select Player Wrapper" :show-label="false" prop="playerWrap">
                      <input type="text" placeholder="Enter (id or class) player wrapper" v-model="playerWrap" />
                    </FormItem>
                  </div>
                </FormItem>
                <template v-for="(value, key) in adTagUrl">
                  <FormItem label="Ad Tag URL" single :key="key">
                    <div class="h-input-group">
                      <FormItem label="Ad Tag URL" :show-label="false">
                        <input type="text" placeholder="URL" v-model="adTagUrl[key]" />
                      </FormItem>
                      <span class="action h-input-addon bg-red-color white-color" v-tooltip content="Remove Item" @click="removeTodo(value)"><i class="h-icon-minus"></i></span>
                    </div>
                  </FormItem>
                </template>
                <FormItem>
                  <Button color="primary" v-tooltip content="Add Item" icon="h-icon-plus" @click="addTodo">Add Url</Button>
                  <Button class="submit" color="blue" :loading="isLoading" @click="runAds">submit</Button>
                </FormItem>
              </Form>
          
          </div>

        </div>
    </section>

    <section class="col-full">
      <div class="container">
        <div class="row part center-xs">
          <div class="col-xs-12 col-sm-12 col-md-8">
            <prism-editor :code="code" language="html" :readonly="true"></prism-editor>
          </div>
          <div class="col-xs-12 col-sm-12 col-md-8">
            <div class="box" id="adsBox" ref="adsBox">
              <div id="playerWrap">
                <div id="player" data-plyr-provider="vimeo" data-plyr-embed-id="76979871"></div>
              </div>
              <div id="console">
                <div id="console-name">event</div>
                <div id="event" ref="event">
                </div>
